.title {
    width: 80%;
    margin: 0 auto;

    font-family: $ubuntu;
    color: $darktext;
    font-size: 30px;
    font-weight: 700;
    text-align: center;
}

@media (max-width: 768px) {
	.title {
		font-size: 22px;
	}
}

@media (max-width: 480px) {
	.title {
		font-size: 16px;
	}
}
